import { ISiHuaInfo, IStarAtlasInfo } from '../StarAtlas/StarAtlas';
import { StarInfo, IPalaceInfo, LiuStarInfo } from '../Palace/Palace';
import { TianGan, DiZhi, StarSign } from './Terms';
import { SiHua, SiHuaTable, SiHuaSeq } from './SiHua';
import { range } from '../Utils/Utils';
import { DiZhiSeq, PalaceSeq, TianGanSeq, LuCunZhi } from './Rules';
import { XianName, IYunXianState } from '../YunXian/YunXian';
import LunarCalendar from 'lunar-calendar';

type PalaceInfoDict = { [key: string]: IPalaceInfo };

export function calculateSiHua(siHuaInfo: ISiHuaInfo, palaces: PalaceInfoDict) {
    const updateSiHua = (s:StarInfo, g: TianGan|undefined, uf: number|{(s: StarInfo, v: SiHua): void}) => {
        if (!g) {
            return;
        }

        if (typeof uf === 'number' && s.siHuas.length <= uf) {
            range(uf - s.siHuas.length + 1).forEach(() => s.siHuas.push(SiHua.PlaceHolder));
        }

        const siHua = SiHuaTable[g];
        siHua.forEach((sh, i) => {
            if (s.starSign === sh) {
                if (typeof uf === 'number') {
                    s.siHuas[uf] = SiHuaSeq[i];
                } else {
                    uf(s, SiHuaSeq[i]);
                }
            }
        });
    }

    const updatePalaceName = (zhi: DiZhi|undefined) => {
        if (!zhi) {
            return;
        }
        const palace = Object.values(palaces).find(p => p.diZhi === zhi);
        if (palace) {
            const mingPalaceIndex = DiZhiSeq.indexOf(palace.diZhi);
            for (let i = 0; i < 12; i++) {
                const index = (i + mingPalaceIndex) % 12;
                const palaceInfo = palaces[DiZhiSeq[index]];
    
                const name = PalaceSeq[(12 - i) % 12];
                palaceInfo.palaceNames.push(name);
            }
        }
    }

    Object.values(palaces).forEach(palace => {
        palace.starSigns1.forEach(s => {
            updateSiHua(s, siHuaInfo.birthGan, 0);
            updateSiHua(s, siHuaInfo.yunGan, 1);
            updateSiHua(s, siHuaInfo.yearGan, 2);   
            updateSiHua(s, siHuaInfo.monthGan, 3);
            updateSiHua(s, siHuaInfo.dayGan, 4);
            updateSiHua(s, siHuaInfo.selectedGan, (s, v) => s.selectedSiHua = v);
        });
    });

    updatePalaceName(siHuaInfo.yunZhi);
    updatePalaceName(siHuaInfo.yearZhi);
    updatePalaceName(siHuaInfo.monthZhi);
    updatePalaceName(siHuaInfo.dayZhi);

    const enabledYun = siHuaInfo.enabledYunXian.slice(-3);

    if (siHuaInfo.yunGan && enabledYun.includes(XianName.DaXian)) {
        calculateLiuLuYangTouPalaces(XianName.DaXian, siHuaInfo.yunGan, StarSign.YunLu, StarSign.YunYan, StarSign.YunTuo, palaces);
    }

    if (siHuaInfo.yearGan && enabledYun.includes(XianName.LiuNian)) {
        calculateLiuLuYangTouPalaces(XianName.LiuNian, siHuaInfo.yearGan, StarSign.LiuLu, StarSign.LiuYan, StarSign.LiuTuo, palaces);
    }

    if (siHuaInfo.monthGan && enabledYun.includes(XianName.LiuYue)) {
        calculateLiuLuYangTouPalaces(XianName.LiuYue, siHuaInfo.monthGan, StarSign.YueLu, StarSign.YueYan, StarSign.YueTuo, palaces);
    }

    if (siHuaInfo.dayGan && enabledYun.includes(XianName.LiuRi)) {
        calculateLiuLuYangTouPalaces(XianName.LiuRi, siHuaInfo.dayGan, StarSign.RiLu, StarSign.RiYan, StarSign.RiTuo, palaces);
    }
}

function calculateLiuLuYangTouPalaces(xian: XianName, yearGan: TianGan, lu: StarSign, yang: StarSign, tuo: StarSign, palaces: PalaceInfoDict) {
    const palace = palaces[LuCunZhi[yearGan]];
    palace.starSigns4.push(new LiuStarInfo(lu, xian));

    const luChunIndex = DiZhiSeq.indexOf(LuCunZhi[yearGan]);
    
    const yangIndex = (luChunIndex + 1) % 12;
    const yangPalace = palaces[DiZhiSeq[yangIndex]];
    yangPalace.starSigns4.push(new LiuStarInfo(yang, xian));

    const tuoIndex = (luChunIndex - 1 + 12) % 12;
    const tuoPalace = palaces[DiZhiSeq[tuoIndex]];
    tuoPalace.starSigns4.push(new LiuStarInfo(tuo, xian));
}

export function calculateYunSiHua(yunState: IYunXianState, starAtlasInfo: IStarAtlasInfo, year: number, palaces: { [key: string]: IPalaceInfo }) {
    const siHua: ISiHuaInfo = { 
        birthGan: starAtlasInfo.birthYearGan,
        enabledYunXian: yunState.enabledYunXian
    };

    let sanFangZhi = undefined;
    yunState.enabledYunXian.forEach(y => {
        switch(y) {
            case XianName.DaXian:
                const yunPalace = Object.values(palaces).find(p => p.daXian === yunState.yun);
                if (yunPalace) {
                    siHua.yunGan = yunPalace.tianGan;
                    siHua.yunZhi = yunPalace.diZhi;
                    sanFangZhi = yunPalace.diZhi;
                }
            break;
            case XianName.LiuNian:
                const { gan, zhi } = calculateYearGanZhi(year);
                const yearPalace = Object.values(palaces).find(p => p.diZhi === zhi);
                if (yearPalace) {
                    siHua.yearGan = gan;
                    siHua.yearZhi = zhi;
                    sanFangZhi = zhi;
                }
            break;
            case XianName.LiuYue:
                if (starAtlasInfo.month !== undefined && starAtlasInfo.hour !== undefined) {
                    const result = calculateYearGanZhi(year);
                    const index = (DiZhiSeq.indexOf(result.zhi) - starAtlasInfo.month + starAtlasInfo.hour + yunState.month + 12) % 12;
                    const zhi = DiZhiSeq[index];

                    const monthPalace = Object.values(palaces).find(p => p.diZhi === zhi);
                    if (monthPalace) {
                        const solarDate = LunarCalendar.lunarToSolar(year, yunState.month + 1, 1);
                        const lunarDate = LunarCalendar.solarToLunar(solarDate.year, solarDate.month, solarDate.day);
                        siHua.monthGan = lunarDate.GanZhiMonth[0];
                        siHua.monthZhi = monthPalace.diZhi;
                        sanFangZhi = zhi;
                    }
                }
            break;
            case XianName.LiuRi:
                if (starAtlasInfo.month !== undefined && starAtlasInfo.hour !== undefined) {
                    const result = calculateYearGanZhi(year);
                    const index = (DiZhiSeq.indexOf(result.zhi) - starAtlasInfo.month + starAtlasInfo.hour + yunState.month + yunState.day + 12) % 12;
                    const zhi = DiZhiSeq[index];

                    const monthPalace = Object.values(palaces).find(p => p.diZhi === zhi);
                    if (monthPalace) {
                        const solarDate = LunarCalendar.lunarToSolar(year, yunState.month + 1, yunState.day + 1);
                        const lunarDate = LunarCalendar.solarToLunar(solarDate.year, solarDate.month, solarDate.day);
                        siHua.dayGan = lunarDate.GanZhiDay[0];
                        siHua.dayZhi = monthPalace.diZhi;
                        sanFangZhi = zhi;
                    }
                }
            break;
        }
    });

    return { siHua, sanFangZhi };
}

function calculateYearGanZhi(year: number) {
    const gan = TianGanSeq[((year - 3) % 10 -1 + 10) % 10];
    const zhi = DiZhiSeq[((year - 3) % 12 -1 + 12) % 12];
    return { gan, zhi };
}