import React, { Component } from 'react';

import './App.css';

import LunarCalendar from 'lunar-calendar';
import { StarAtlas, IStarAtlasInfo } from './StarAtlas/StarAtlas';
import { Gender } from './AtlasBuilder/Terms';

class App extends Component {
    public render() {
        const a1 = this.getInfo(1983, 2, 25, 4, Gender.Male);
        const a2 = this.getInfo(1987, 9, 5, 16, Gender.Female);
        const a3 = this.getInfo(1990, 2, 24, 23, Gender.Female);
        const a6 = this.getInfo(1997, 7, 7, 10, Gender.Female);
        const a4: IStarAtlasInfo = {
            birthYear: 0,
            month: 9,
            day: 5,
            hour: 6,
        }
        const today = new Date();
        const a5 = this.getInfo(today.getFullYear(), today.getMonth(), today.getDate(), today.getHours(), Gender.Male)

        return (
            <div className="board">
                <StarAtlas {...a1}></StarAtlas>
                <StarAtlas {...a2}></StarAtlas>
                <StarAtlas {...a3}></StarAtlas>
                <StarAtlas {...a6}></StarAtlas>
                <StarAtlas {...a4}></StarAtlas>
                <StarAtlas {...a5}></StarAtlas>
            </div>
        );
    }

    private getInfo(year: number, month: number, day: number, hour: number, gender: Gender): IStarAtlasInfo {
        const lunarDate = LunarCalendar.solarToLunar(year, month, day);
        
        month = lunarDate.lunarMonth - 1;
        if (lunarDate.lunarLeapMonth > 0 && month > lunarDate.lunarLeapMonth) {
            month -= 1;
        }

        day = Math.round(lunarDate.lunarDay);
        hour = Math.floor(hour / 2)

        console.log(lunarDate, month, day, hour)

        return {
            birthYear: lunarDate.lunarYear,
            birthYearGan: lunarDate.GanZhiYear[0],
            birthYearZhi: lunarDate.GanZhiYear[1],
            month: month,
            day: Math.round(lunarDate.lunarDay),
            hour: hour,
            gender: gender,
        }
    }
}

export default App;
    