import React, { Component, CSSProperties } from 'react';
import { format } from 'date-fns';
import LunarCalendar from 'lunar-calendar';

import './StarAtlas.scss'

import { Palace, IPalaceInfo } from '../Palace/Palace';
import { DiZhi, TianGan, Gender } from '../AtlasBuilder/Terms';
import { buildStartAtlas, calculateWuXingNumber as calculateWuXingJu, calculateMingPalaceDiZhi } from '../AtlasBuilder/AtlasBuilder';
import { DiZhiSeq, FengZhengPos as FangZhengPos } from '../AtlasBuilder/Rules';
import { YunXian, IYunXianState } from '../YunXian/YunXian';
import { calculateSiHua, calculateYunSiHua } from '../AtlasBuilder/YunXianBuilder';

export class StarAtlas extends Component<IStarAtlasInfo, IStartAtlasState> {

    constructor(atlasInfo: Readonly<IStarAtlasInfo>) {
        super(atlasInfo);

        const selectedDiZhi = this.props.month !== undefined && this.props.hour !== undefined
            ? calculateMingPalaceDiZhi(this.props.month, this.props.hour)
            : DiZhi.Yin;

        const siHua: ISiHuaInfo = {
            birthGan: atlasInfo.birthYearGan,
            enabledYunXian: []
        }

        const palaces = buildStartAtlas(this.props);
        calculateSiHua(siHua, palaces);

        this.state = {
            palaces,
            siHua,
            selectedDiZhi
        };
    }

    public render() {
        const wuXing = calculateWuXingJu(this.props.birthYearGan, this.props.month, this.props.hour);
        const sanFangDiZhi = this.state.selectedDiZhi;
        return (
            <div className="atlas">
                <table className="atlas-table">
                    <tbody>
                        <tr>
                            <td>{this.getPalace(DiZhi.Si)}</td>
                            <td>{this.getPalace(DiZhi.Wu)}</td>
                            <td>{this.getPalace(DiZhi.Wei)}</td>
                            <td>{this.getPalace(DiZhi.Shen)}</td>
                        </tr>
                        <tr>
                            <td>{this.getPalace(DiZhi.Chen)}</td>
                            <td colSpan={2} rowSpan={2} className="center" >
                                {this.getInfo()}
                                {this.createSanHeLines(sanFangDiZhi)}
                            </td>
                            <td>{this.getPalace(DiZhi.You)}</td>
                        </tr>
                        <tr>
                            <td>{this.getPalace(DiZhi.Mou)}</td>
                            <td>{this.getPalace(DiZhi.Xu)}</td>
                        </tr>
                        <tr>
                            <td>{this.getPalace(DiZhi.Yin)}</td>
                            <td>{this.getPalace(DiZhi.Chou)}</td>
                            <td>{this.getPalace(DiZhi.Zi)}</td>
                            <td>{this.getPalace(DiZhi.Hai)}</td>
                        </tr>
                    </tbody>
                </table>
                <YunXian className="time-table" 
                         birthYear={this.props.birthYear}
                         wuXingNumber={wuXing}
                         change = {x => this.onYunChanged(x)}>
                </YunXian>
            </div>);
    }

    private getPalace(dizhi: DiZhi) {
        const info = this.state.palaces[dizhi];
        return (<Palace {...info} onClick={() => this.onPalaceClick(info.tianGan, info.diZhi)}></Palace>);
    }

    private onPalaceClick(tianGan: TianGan|undefined, diZhi: DiZhi) {
        if (!tianGan) {
            return;
        }

        const siHua = { ...this.state.siHua };
        siHua.selectedGan = siHua.selectedGan === tianGan ? undefined : tianGan;

        const palaces = buildStartAtlas(this.props);
        calculateSiHua(siHua, palaces);

        this.setState({
            siHua,
            palaces,
            selectedDiZhi: diZhi
        });
    }

    private getInfo() {
        let info = '';

        if (this.props.birthYearGan && this.props.birthYearZhi) {
            info += `${this.props.birthYearGan}${this.props.birthYearZhi}`;
        }
        
        if (this.props.month !== undefined) {
            info += ` ${this.props.month + 1}`;
        }

        if (this.props.day !== undefined) {
            info += ` ${this.props.day}`;
        }

        if (this.props.hour !== undefined) {
            info += ` ${DiZhiSeq[this.props.hour]}`;
        }
        
        const today = this.state.solarDate 
            ? format(this.state.solarDate, 'YYYY MM DD ddd')
            : undefined;

        return (
            <span className="info">
                <div>{info}</div>
                <div className="today">{today}</div>
            </span>
        );
    }

    private createSanHeLines(louZhi: DiZhi | undefined) {
        if (!louZhi) {
            return;
        }

        if (!this.state.siHua.birthGan) {
            return undefined;
        }
        
        const zhiPos = FangZhengPos[louZhi];
        const sanFang1Pos = FangZhengPos[DiZhiSeq[(DiZhiSeq.indexOf(louZhi) + 4) % 12]];
        const sanFang2Pos = FangZhengPos[DiZhiSeq[(DiZhiSeq.indexOf(louZhi) + 8) % 12]];
        const siZhengPos = FangZhengPos[DiZhiSeq[(DiZhiSeq.indexOf(louZhi) + 6) % 12]];

        const sanFang1 = drawLine(zhiPos, sanFang1Pos);
        const sanFang2 = drawLine(zhiPos, sanFang2Pos);
        const sanFang3 = drawLine(sanFang1Pos, sanFang2Pos);
        const siZeng = drawLine(zhiPos, siZhengPos);

        return [
            <div key="sanfang-1" style={sanFang1}></div>,
            <div key="sanfang-2" style={sanFang2}></div>,
            <div key="sanfang-3" style={sanFang3}></div>,
            <div key="sizheng-1" style={siZeng}></div>
        ];
    }

    // todo: move to builder function
    private onYunChanged(yunState: IYunXianState) {
        const year = this.props.birthYear + yunState.yun + yunState.year - 1;
        const palaces = buildStartAtlas(this.props);

        const { siHua, sanFangZhi } = calculateYunSiHua(yunState, this.props, year, this.state.palaces);
        calculateSiHua(siHua, palaces);

        var solarDate = LunarCalendar.lunarToSolar(year, yunState.month + 1, yunState.day + 1);
        this.setState({
            palaces,
            siHua,
            selectedDiZhi: sanFangZhi, 
            solarDate: new Date(solarDate.year, solarDate.month -1, solarDate.day)
        });
    }
}

export interface IStarAtlasInfo {
    birthYear: number;
    birthYearGan?: TianGan;
    birthYearZhi?: DiZhi;
    month?: number;
    day?: number;
    hour?: number;
    gender?: Gender;
    ziWeiZhi?: DiZhi;
    daXian?: number;
}

export interface IStartAtlasState {
    palaces: { [key: string]: IPalaceInfo };
    siHua: ISiHuaInfo;
    selectedDiZhi?: DiZhi;
    yunXian?: IYunXianState;
    solarDate?: Date;
}

export interface ISiHuaInfo {
    birthGan?: TianGan,
    selectedGan?: TianGan,
    yunGan?: TianGan,
    yunZhi?: DiZhi,
    yearGan?: TianGan,
    yearZhi?: DiZhi,
    monthGan?: TianGan,
    monthZhi?: DiZhi,
    dayGan?: TianGan,
    dayZhi?: DiZhi,
    enabledYunXian: number[]
}

function drawLine (pos1: [number, number], pos2: [number, number]): CSSProperties {
    const width = Math.abs(pos2[0] - pos1[0]) / 4 * 100;
    const height = Math.abs(pos2[1] - pos1[1]) / 4 * 100;
    const left = Math.min(pos2[0], pos1[0]) / 4 * 100;
    const bottom = Math.min(pos2[1], pos1[1]) / 4 * 100;

    const background = pos1[0] > pos2[0] && pos1[1] < pos2[1] || pos1[0] < pos2[0] && pos1[1] > pos2[1]
        ? 'url("data:image/svg+xml;base64,") no-repeat center center'
        : 'url("data:image/svg+xml;base64,") no-repeat center center';

    return {
        position: 'absolute',
        width: `${width}%`,
        height: `${height}%`,
        left: `${left}%`,
        bottom: `${bottom}%`,
        background: background
    }
}