import React, { Component } from 'react';

import './Palace.scss';

import { DiZhi, TianGan, Brightness, StarSign, PalaceName } from '../AtlasBuilder/Terms';
import { StarBrightness } from './StarBrightness';
import { SiHua } from '../AtlasBuilder/SiHua';
import { XianName } from '../YunXian/YunXian';

export class Palace extends Component<IPalaceInfo> {
    private readonly yunLimit = 3;

    private readonly siHuaCssClasses: { [k: string]: string } = {
        [SiHua.Lu]: 'lu',
        [SiHua.Quan]: 'quan',
        [SiHua.Ke]: 'ke',
        [SiHua.Ji]: 'ji',
    }

    private readonly yunCssClasses: { [k: number]: string } = {
        [0]: 'ming',
        [1]: 'yun',
        [2]: 'year',
        [3]: 'month',
        [4]: 'day',
    }

    public render() {
        const starSigns1 = this.props.starSigns1.map(s => this.getMainStarSign(s));
        const starSigns2 = this.props.starSigns2.map(s => this.getStarSign(s));
        const starSigns3 = this.props.starSigns3.map(s => this.getStarSign(s));
        const starSigns4 = this.props.starSigns4.map(s => this.getLiuStarSign(s));

        const shenPalace = this.props.isShenPalace 
            ? (<span className="shen-palace">{this.props.isShenPalace ? '': ''}</span>)
            : undefined;

        const daXian = this.props.daXian 
            ? (<span className="da-xian">{this.props.daXian}-{this.props.daXian + 10}</span>) 
            : undefined;

        return (
            <div className="palace" onClick={this.props.onClick}>
                <div className="row">
                    <div className="level-1-stars">{starSigns1}{starSigns2}</div>
                    <div className="level-2-stars">{starSigns3}</div>
                </div>
                <div className="row yun-row">
                    {starSigns4}
                </div>
                <div className="bottom-row">        
                    <span className="palace-name">
                        {this.getPalaceNames(this.props.palaceNames || [])}
                    </span>
                    {shenPalace}
                    <div className="right-col">
                        {daXian}
                        <span className="di-zhi">
                            <span>{this.props.tianGan}</span>
                            <span>{this.props.diZhi}</span>
                        </span>
                    </div>
                </div>
            </div>
        );
    }

    private getMainStarSign(starSign: StarInfo) {
        const allBrightness = StarBrightness[starSign.starSign];
        const brightnessCode = allBrightness ? allBrightness[this.props.diZhi] || Brightness.Ping : undefined;

        const starClass = 'star-sign' + (starSign.selectedSiHua ? ` ${this.siHuaCssClasses[starSign.selectedSiHua]}` : '');
        const siHuaList = starSign.siHuas
            .map((s, i) => {
                const className = 'si-hua' + (s !== SiHua.PlaceHolder ? ` ${this.yunCssClasses[i]}` : '');
                return (<span className={className} key={'sh-' + i}>{s}</span>);
            })
            .slice(-this.yunLimit);

        return (
            <span className="star-sign-info" key={starSign.starSign}>
                <span className={starClass}>{starSign.starSign}</span>
                <span className="bright">{brightnessCode}</span>
                {siHuaList}
            </span>
        );
    }

    private getStarSign(starSign: string) {
        return (
            <span className="star-sign-info" key={starSign}>
                <span className="star-sign">{starSign}</span>
            </span>
        );
    }

    private getLiuStarSign(starInfo: LiuStarInfo) {
        const className = 'star-sign' + ` ${this.yunCssClasses[starInfo.xian + 1]}`;
        return (
            <span className="star-sign-info" key={starInfo.xian + '_' + starInfo.starSign}>
                <span className={className}>{starInfo.starSign}</span>
            </span>
        );
    }

    private getPalaceNames(names: PalaceName[]) {
        return (names)
            .map((s, i) => {
                return s ? (<div className={this.yunCssClasses[i]} key={`pn-${i}`}>{s}</div>) : undefined;
            })
            .filter(s => !!s)
            .slice(-this.yunLimit)
            .reverse();
    }
}

export interface IPalaceInfo {
    starSigns1: StarInfo[];
    starSigns2: string[];
    starSigns3: string[];
    starSigns4: LiuStarInfo[];
    tianGan?: TianGan;
    diZhi: DiZhi;
    palaceNames: PalaceName[];
    isShenPalace?: boolean;
    daXian?: number;
    yearGan?: TianGan;
    onClick?: {():void};
}

export class StarInfo {
    public siHuas: SiHua[] = [];
    public selectedSiHua?: SiHua;
    constructor(public starSign: StarSign) {}
}

export class LiuStarInfo {
    constructor(public starSign: StarSign, public xian: XianName) {}
}