import { IStarAtlasInfo } from '../StarAtlas/StarAtlas';
import { IPalaceInfo, StarInfo } from '../Palace/Palace';
import { DiZhi, TianGan, StarSign, Gender } from './Terms';
import { DiZhiSeq, PalaceSeq, WuHuDunYin, TianGanSeq, WuXingJu, NMainStarSeq, SMainStarSeq, LuCunZhi, WuXingJuSeq, WuXingNumber, WuXingDiZhi, KuiYuePos, TianMaPos, HuoLingPos, JieKongPos } from './Rules';

type PalaceInfoDict = { [key: string]: IPalaceInfo };

export function buildStartAtlas(atlasInfo: IStarAtlasInfo) {
    // init 12 palaces
    const palaces = createInitialPalaces(atlasInfo);

    // place di zhi tian gan
    if (atlasInfo.birthYearGan) {
        calculateYinGan(atlasInfo.birthYearGan, palaces);
    }

    // place ming and shen palace
    if (atlasInfo.month !== undefined && atlasInfo.month >= 0 
        && atlasInfo.hour !== undefined && atlasInfo.hour >= 0) {
        calculateMingShenPalace(atlasInfo.month, atlasInfo.hour, palaces);

        if (atlasInfo.birthYearGan && atlasInfo.gender) {
            calculateDaXian(atlasInfo.birthYearGan, atlasInfo.month, atlasInfo.hour, atlasInfo.gender, palaces);
        }
    }

    // place star sign
    let ziWeiZhi = atlasInfo.ziWeiZhi;
    
    if (!ziWeiZhi && atlasInfo.birthYearGan 
        && atlasInfo.month !== undefined && atlasInfo.day !== undefined && atlasInfo.hour !== undefined) {
        ziWeiZhi = calculateZiWeiPalace(atlasInfo.birthYearGan, atlasInfo.month, atlasInfo.day, atlasInfo.hour);
    }

    if (ziWeiZhi){
        calculateMainStarSigns(ziWeiZhi, palaces);
    }

    if (atlasInfo.birthYearGan) {
        calculateLuCunPalace(atlasInfo.birthYearGan, palaces);
        calculateKuiYuePalaces(atlasInfo.birthYearGan, palaces);
        calculateYangTouPalaces(atlasInfo.birthYearGan, palaces);
        calculateJieKongPalaces(atlasInfo.birthYearGan, palaces);
        calculateXunKongPalaces(atlasInfo.birthYearGan, palaces);
    }

    if (atlasInfo.birthYearZhi) {
        calculateTianMaPalace(atlasInfo.birthYearZhi, palaces);

        if (atlasInfo.hour !== undefined) {
            calculateHuoLingPalace(atlasInfo.birthYearZhi, atlasInfo.hour, palaces);
        }

        calculateLongFengPalace(atlasInfo.birthYearZhi, palaces);
        calcuateLuanXiPalaces(atlasInfo.birthYearZhi, palaces);
        calcuateDaHaoPalaces(atlasInfo.birthYearZhi, palaces);
        calculateHuaGai(atlasInfo.birthYearZhi, palaces);
    }

    if (atlasInfo.month !== undefined) {
        calculateFuBiPalaces(atlasInfo.month, palaces);
        calculateXingYaoPalaces(atlasInfo.month, palaces);

        if (atlasInfo.day !== undefined) {
            calculateTaiZuoPalaces(atlasInfo.month, atlasInfo.day, palaces);
        }

        calcuateYueJieTianWuPalaces(atlasInfo.month, palaces);
        calculateYinShaPalace(atlasInfo.month, palaces);
        calculateTianYue2Palace(atlasInfo.month, palaces);
    }

    if (atlasInfo.hour !== undefined) {
        calculateQuChangPalaces(atlasInfo.hour, palaces);
        calculateKongJiePalaces(atlasInfo.hour, palaces);

        if (atlasInfo.day !== undefined) {
            calculateNenGuiPalaces(atlasInfo.hour, atlasInfo.day, palaces);
        }
    }

    return palaces;
}

function createInitialPalaces(atlasInfo: IStarAtlasInfo) {
    const palaces: PalaceInfoDict = {};

    for (const key in DiZhi) {
        const dizhi = DiZhi[key] as DiZhi;
        const palaceInfo: IPalaceInfo = {
            starSigns1: [],
            starSigns2: [],
            starSigns3: [],
            starSigns4: [],
            diZhi: dizhi,
            palaceNames: [],
            yearGan: atlasInfo.birthYearGan
        };

        palaces[dizhi] = palaceInfo;
    }

    return palaces;
}

function calculateYinGan(yearGan: TianGan, palaces: PalaceInfoDict) {
    const startGanIndex = TianGanSeq.indexOf(WuHuDunYin[yearGan]);
    for (let i = 0; i < 12; i++) {
        const palaceInfo = palaces[DiZhiSeq[(i + 2) % 12]];
        palaceInfo.tianGan = TianGanSeq[(startGanIndex + i) % 10];
    }
}

function calculateMingShenPalace(month: number, hour: number, palaces: PalaceInfoDict) {
    const mingPalaceIndex = (DiZhiSeq.indexOf(DiZhi.Yin) + month - hour + 12) % 12;
    const shenPalaceIndex = (DiZhiSeq.indexOf(DiZhi.Yin) + month + hour) % 12;  
    for (let i = 0; i < 12; i++) {
        const index = (i + mingPalaceIndex) % 12;
        const palaceInfo = palaces[DiZhiSeq[index]];

        const name = PalaceSeq[(12 - i) % 12]
        palaceInfo.palaceNames = [name];
        palaceInfo.isShenPalace = index === shenPalaceIndex;
    }
}

function calculateDaXian(yearGan: TianGan, month: number, hour: number, gender: Gender, palaces: PalaceInfoDict) {
    const mingIndex = (DiZhiSeq.indexOf(DiZhi.Yin) + month - hour + 12) % 12;
    
    const wuXing = calulcateWuXinJu(yearGan, DiZhiSeq[mingIndex]);
    const wuXingNum = WuXingNumber[wuXing];
    
    const countFunc = makeCountFunc(yearGan, gender);
    for (let i = 0; i < 12; i++) {
        const index = countFunc(i, mingIndex);
         
        const palaceInfo = palaces[DiZhiSeq[index]];
        palaceInfo.daXian = wuXingNum + i * 10;
    }
}

export function makeCountFunc(yearGan: TianGan, gender: Gender) {
    const clockwise = 
        (TianGanSeq.indexOf(yearGan) % 2 === 0 && gender === Gender.Male) ||
        (TianGanSeq.indexOf(yearGan) % 2 === 1 && gender === Gender.Female);

    return clockwise 
        ? (i: number, mingIndex: number) => (i + mingIndex) % 12 
        : (i: number, mingIndex: number) => (mingIndex - i + 12) % 12;
}

function calculateMainStarSigns(ziWeiZhi: DiZhi, palaces: PalaceInfoDict) {
    const ziweiIndex = DiZhiSeq.indexOf(ziWeiZhi);
    for (let i = 0; i < 12; i++) {
        const starSign = NMainStarSeq[i];
        if (!starSign) {
            continue;
        }

        const index = (ziweiIndex - i + 12) % 12;
        const palace = palaces[DiZhiSeq[index]];
        palace.starSigns1.push(new StarInfo(starSign));
    }

    const tianFuIndex = (12 - (DiZhiSeq.indexOf(ziWeiZhi) - 2 + 12) % 12) % 12 + 2;
    for (let i = 0; i < 12; i++) {
        const starSign = SMainStarSeq[i];
        if (!starSign) {
            continue;
        }

        const index = (tianFuIndex + i) % 12;
        const palace = palaces[DiZhiSeq[index]];
        palace.starSigns1.push(new StarInfo(starSign));
    }
}

function calculateLuCunPalace(yearGan: TianGan, palaces: PalaceInfoDict) {
    const palace = palaces[LuCunZhi[yearGan]];
    palace.starSigns1.push(new StarInfo(StarSign.LuCun));
}

function calculateYangTouPalaces(yearGan: TianGan, palaces: PalaceInfoDict) {
    const luChunIndex = DiZhiSeq.indexOf(LuCunZhi[yearGan]);
    
    const yangIndex = (luChunIndex + 1) % 12;
    const yangPalace = palaces[DiZhiSeq[yangIndex]];
    yangPalace.starSigns2.push(StarSign.QingYang);

    const tuoIndex = (luChunIndex - 1 + 12) % 12;
    const tuoPalace = palaces[DiZhiSeq[tuoIndex]];
    tuoPalace.starSigns2.push(StarSign.TuoLuo);
}

function calculateKuiYuePalaces(yearGan: TianGan, palaces: PalaceInfoDict) {
    const kuiPalace = palaces[KuiYuePos[yearGan][0]];
    kuiPalace.starSigns2.push(StarSign.TianKui)

    const yuePalace = palaces[KuiYuePos[yearGan][1]];
    yuePalace.starSigns2.push(StarSign.TianYue)
}

function calculateJieKongPalaces(yearGan: TianGan, palaces: PalaceInfoDict) {
    const kuiPalace = palaces[JieKongPos[yearGan]];
    kuiPalace.starSigns3.push(StarSign.JieKong)
}

function calculateXunKongPalaces(_yearGan: TianGan, palaces: PalaceInfoDict) {
    const guiGanZhi = Object.keys(palaces).filter(k => palaces[k].tianGan === TianGan.Gui)[0];
    const guiPalace = palaces[guiGanZhi];
    const xunKongIndex = DiZhiSeq.indexOf(guiPalace.diZhi) + 1;
    const xunKongPalace = palaces[DiZhiSeq[xunKongIndex % 12]];
    xunKongPalace.starSigns3.push(StarSign.XunKong);
}

function calcuateLuanXiPalaces(yearZhi: DiZhi, palaces: PalaceInfoDict) {
    const hongLuanIndex = DiZhiSeq.indexOf(DiZhi.Mou) - DiZhiSeq.indexOf(yearZhi) + 12;
    const hongLuanPalace = palaces[DiZhiSeq[hongLuanIndex % 12]];
    hongLuanPalace.starSigns2.push(StarSign.HongLuan);

    const tianXiPalace = palaces[DiZhiSeq[(hongLuanIndex + 6) % 12]];
    tianXiPalace.starSigns2.push(StarSign.TianXi);
}

function calcuateDaHaoPalaces(yearZhi: DiZhi, palaces: PalaceInfoDict) {
    const duiPalaceIndex = DiZhiSeq.indexOf(yearZhi) + 6;
    const daHaoIndex = duiPalaceIndex % 2 === 0 ? duiPalaceIndex + 1 : duiPalaceIndex -1;
    const daHaoPalace = palaces[DiZhiSeq[daHaoIndex % 12]];
    daHaoPalace.starSigns3.push(StarSign.DaHao);
}

function calculateHuaGai(yearZhi: DiZhi, palaces: PalaceInfoDict) {
    const seq = [DiZhi.Chen, DiZhi.Chou, DiZhi.Wu, DiZhi.Wei];
    const index = DiZhiSeq.indexOf(yearZhi) % 4;
    const palace = palaces[seq[index]];
    palace.starSigns3.push(StarSign.HuaGai);
}

function calculateQuChangPalaces(hour: number, palaces: PalaceInfoDict) {
    const wenChangIndex = (DiZhiSeq.indexOf(DiZhi.Xu) - hour + 12) % 12;
    const wenChangPalace = palaces[DiZhiSeq[wenChangIndex]];
    wenChangPalace.starSigns1.push(new StarInfo(StarSign.WenChang));

    const wenQuIndex = (DiZhiSeq.indexOf(DiZhi.Chen) + hour) % 12;
    const wenQuPalace = palaces[DiZhiSeq[wenQuIndex]];
    wenQuPalace.starSigns1.push(new StarInfo(StarSign.WenQu));
}

function calculateNenGuiPalaces(hour: number, day: number, palaces: PalaceInfoDict) {
    const wenChangIndex = (DiZhiSeq.indexOf(DiZhi.Xu) - hour + 12) % 12;
    const nenGuangPalace = palaces[DiZhiSeq[(wenChangIndex + day - 2) % 12]];
    nenGuangPalace.starSigns3.push(StarSign.NenGuang);

    const wenQuIndex = (DiZhiSeq.indexOf(DiZhi.Chen) + hour) % 12;
    const tianGuiPalace = palaces[DiZhiSeq[(wenQuIndex + day - 2) % 12]];
    tianGuiPalace.starSigns3.push(StarSign.TianGui);
}

function calculateFuBiPalaces(month: number, palaces: PalaceInfoDict) {
    const youBiIndex = (DiZhiSeq.indexOf(DiZhi.Xu) - month + 12) % 12;
    const youBiPalace = palaces[DiZhiSeq[youBiIndex]];
    youBiPalace.starSigns1.push(new StarInfo(StarSign.YouBi));

    const zuoFuIndex = (DiZhiSeq.indexOf(DiZhi.Chen) + month) % 12;
    const zuoFuPalace = palaces[DiZhiSeq[zuoFuIndex]];
    zuoFuPalace.starSigns1.push(new StarInfo(StarSign.ZuoFu));
}

function calculateTaiZuoPalaces(month: number, day: number, palaces: PalaceInfoDict) {
    const sanTaiIndex = (DiZhiSeq.indexOf(DiZhi.Chen) + month + day - 1) % 12;
    const sanTaiPalace = palaces[DiZhiSeq[sanTaiIndex]];
    sanTaiPalace.starSigns3.push(StarSign.SanTai);

    const baZuoIndex = (DiZhiSeq.indexOf(DiZhi.Xu) - month - day + 1) % 12 + 12;
    const baZuoPalace = palaces[DiZhiSeq[baZuoIndex % 12]];
    baZuoPalace.starSigns3.push(StarSign.BaZuo);
}

function calculateXingYaoPalaces(month: number, palaces: PalaceInfoDict) {
    const xingIndex = (DiZhiSeq.indexOf(DiZhi.You) + month) % 12;
    const xingPalace = palaces[DiZhiSeq[xingIndex]];
    xingPalace.starSigns2.push(StarSign.TianXing);

    const yaoIndex = (DiZhiSeq.indexOf(DiZhi.Chou) + month) % 12;
    const yaoPalace = palaces[DiZhiSeq[yaoIndex]];
    yaoPalace.starSigns2.push(StarSign.TianYao);
}

function calcuateYueJieTianWuPalaces(month: number, palaces: PalaceInfoDict) {
    const jieIndex = DiZhiSeq.indexOf(DiZhi.Shen) + month * Math.floor(month / 2);
    const jiePalace = palaces[DiZhiSeq[jieIndex % 12]];
    jiePalace.starSigns3.push(StarSign.JieShen)

    const seq = [DiZhi.Si, DiZhi.Shen, DiZhi.Yin, DiZhi.Hai];
    const tianWuZhi = seq[Math.floor(month / 3)];
    const tianWuPalace = palaces[tianWuZhi];
    tianWuPalace.starSigns3.push(StarSign.TianWu)
}

function calculateYinShaPalace(month: number, palaces: PalaceInfoDict) {
    const index = 2 - (month * 2) % 12 + 12;
    const palace = palaces[DiZhiSeq[index % 12]];
    palace.starSigns3.push(StarSign.YinSha);
}

function calculateTianYue2Palace(month: number, palaces: PalaceInfoDict) {
    const seq = [DiZhi.Xu, DiZhi.Si, DiZhi.Chen, DiZhi.Yin, DiZhi.Wei, DiZhi.Mou,
                   DiZhi.Hai, DiZhi.Wei, DiZhi.Yin, DiZhi.Wu, DiZhi.Xu, DiZhi.Yin];
    const palace = palaces[seq[month]];
    palace.starSigns3.push(StarSign.TianYue2);
}

function calculateTianMaPalace(yearZhi: DiZhi, palaces: PalaceInfoDict) {
    const palace = palaces[TianMaPos[yearZhi]];
    palace.starSigns2.push(StarSign.TianMa)
}

function calculateLongFengPalace(yearZhi: DiZhi, palaces: PalaceInfoDict) {
    const longChiIndex = DiZhiSeq.indexOf(DiZhi.Chen) + DiZhiSeq.indexOf(yearZhi);
    const longChiPalace = palaces[DiZhiSeq[longChiIndex % 12]];
    longChiPalace.starSigns3.push(StarSign.LongChi);

    const fengGeIndex = DiZhiSeq.indexOf(DiZhi.Xu) - DiZhiSeq.indexOf(yearZhi) + 12;
    const fengGePalace = palaces[DiZhiSeq[fengGeIndex % 12]];
    fengGePalace.starSigns3.push(StarSign.FengGe);
}

function calculateHuoLingPalace(yearZhi: DiZhi, hour: number, palaces: PalaceInfoDict) {
    const [huoStartZhi, lingStartZhi] = HuoLingPos[yearZhi]
    
    const huoZhi = DiZhiSeq[(DiZhiSeq.indexOf(huoStartZhi) + hour) % 12]
    const huoPalace = palaces[huoZhi];
    huoPalace.starSigns2.push(StarSign.HuoXing)

    const lingZhi = DiZhiSeq[(DiZhiSeq.indexOf(lingStartZhi) + hour) % 12]
    const lingPalace = palaces[lingZhi];
    lingPalace.starSigns2.push(StarSign.LingXing)
}

function calculateKongJiePalaces(hour: number, palaces: PalaceInfoDict) {
    const diJieZhi = DiZhiSeq[(DiZhiSeq.indexOf(DiZhi.Hai) + hour) % 12];
    const diJiePalace = palaces[diJieZhi];
    diJiePalace.starSigns2.push(StarSign.DiJie);

    const diKongZhi = DiZhiSeq[(DiZhiSeq.indexOf(DiZhi.Hai) - hour + 12) % 12];
    const diKongPalace = palaces[diKongZhi];
    diKongPalace.starSigns2.push(StarSign.DiKong);
}


export function calculateZiWeiPalace(yearGan: TianGan, month: number, day:number, hour: number): DiZhi {
    const mingZhi = calculateMingPalaceDiZhi(month, hour);
    const wuXing = calulcateWuXinJu(yearGan, mingZhi);
    
    const seq = [DiZhi.You, DiZhi.Wu, DiZhi.Hai, DiZhi.Chen, DiZhi.Chou];

    const wuXingNum = WuXingNumber[wuXing];
    const wuXingZhi = WuXingDiZhi[wuXing];

    const startIndex = seq.indexOf(wuXingZhi);

    const shang = Math.floor(day / wuXingNum);
    const remain = day % wuXingNum;

    const startDiZhi = remain === 0 ? DiZhi.Yin : seq[(startIndex + remain - 1) % 5];
    return DiZhiSeq[(DiZhiSeq.indexOf(startDiZhi) + shang) % 12];
}

export function calculateMingPalaceDiZhi(month: number, hour: number) {
    const mingPalaceIndex = (DiZhiSeq.indexOf(DiZhi.Yin) + month -  hour + 12) % 12;
    return DiZhiSeq[mingPalaceIndex];
}

export function calulcateWuXinJu(tianGan: TianGan, diZhi: DiZhi) {
    const startElement = WuXingJu[tianGan];
    const elementIndex = WuXingJuSeq.indexOf(startElement) + Math.floor(DiZhiSeq.indexOf(diZhi) / 2);
    return WuXingJuSeq[elementIndex % 5];
}

export function calculateWuXingNumber(yearGan: TianGan | undefined, month: number | undefined, hour: number | undefined) {
    if (!yearGan || month === undefined || hour === undefined) {
        return 1;
    }

    const mingZhi = calculateMingPalaceDiZhi(month, hour);
    const wuXing = calulcateWuXinJu(yearGan, mingZhi);
    return WuXingNumber[wuXing];
}
